<template>
  <v-container :class="`${$vuetify.breakpoint.xs ? '' : 'main-container'}`">
    <Grid-Contents
      :img="project.thumbnail_image"
      :header="project.name"
      :caption="project.caption"
      :content="project.summary"
    />
    <v-divider class="mb-5"></v-divider>
    <Grid-Contents :img="project.thumbnail_image" />
    <Grid-Contents :img="project.thumbnail_image" />
    <Grid-Contents :img="project.thumbnail_image" />
    <Footer />
  </v-container>
</template>

<script>
import { mapGetters, mapActions } from 'vuex'
import NavigationGroup from '@/components/NavigationGroup'
import GridContents from '@/components/GridContents'
import Footer from '@/components/Footer'

export default {
  components: {
    NavigationGroup,
    GridContents,
    Footer
  },
  props: {
    id: String
  },
  created () {
    this.getProjects(this.id)
  },
  methods: {
    ...mapActions('projects', {
      getProjects: 'getProjects'
    })
  },
  computed: {
    ...mapGetters('projects', {
      project: 'project'
    })
  }
}
</script>
