<template>
  <div id="home">
    <v-container :class="`${$vuetify.breakpoint.xs ? '' : 'main-container'}`">
      <Navigation-Group
        class="mt-4"
        title="SNAPS"
        :list="filters"
        @itemSelect="filterList"
      />
      <Grid-Cards
        :list="snaps"
        :aspectRatio="aspectRatio"
        @itemSelect="openView"
      />
      <v-dialog v-model="dialog" :width="`${activeFilter === 'landscape' ? 900 : 510}px`">
        <v-img :src="pic.thumbnail_image" :aspect-ratio="aspectRatio" @click.native="dialog = false"></v-img>
      </v-dialog>
      <Footer />
    </v-container>
  </div>
</template>

<script>
import { mapGetters } from 'vuex'
import GridCards from '@/components/GridCards'
import NavigationGroup from '@/components/NavigationGroup'
import Footer from '@/components/Footer'

export default {
  components: {
    GridCards,
    NavigationGroup,
    Footer
  },
  data () {
    return {
      filters: [
        { name: 'Fashion', value: 'fashion', active: false },
        { name: 'Landscape', value: 'landscape', active: true }
      ],
      activeFilter: 'landscape',
      aspectRatio: 1.5,
      dialog: false,
      pic: {}
    }
  },
  computed: {
    ...mapGetters('snaps', {
      snapsByBook: 'snapsByBook'
    }),
    snaps () {
      return this.snapsByBook(this.activeFilter)
    }
  },
  methods: {
    filterList (str) {
      this.aspectRatio = str === 'landscape' ? 1.5 : 0.75
      this.activeFilter = str
    },
    openView (obj) {
      this.pic = obj
      this.dialog = true
    }
  }
}
</script>
