<template>
  <div id="navigation-group">
    <v-layout row class="ma-2">
      <p class="text-position headline">
        <b>{{ title }}</b>
        <span v-if="caption" class="secondaryDark--text subheading"><br>{{ caption }}</span>
      </p>
      <v-spacer v-if="position === 'right'"></v-spacer>
      <p
        v-for="(item, index) in itemList"
        v-bind:key="index"
        @click="setActive(index)"
        class="text-position cursor-link"
        :class="[itemSpacing, fontClasses, activeClass(item.active)]"
        :style="itemStyle"
      >
        {{ item.name }}
      </p>
      <v-spacer v-if="position === 'left'"></v-spacer>
    </v-layout>
  </div>
</template>

<script>
export default {
  props: {
    title: {
      type: String,
      default: undefined
    },
    caption: {
      type: String,
      default: undefined
    },
    list: {
      type: Array,
      default: () => { return [] }
    },
    margin: {
      type: Number,
      default: 5
    },
    fontSize: {
      type: String,
      default: 'subheading'
    },
    fontWeight: {
      type: String,
      default: 'regular'
    },
    position: {
      type: String,
      default: 'right'
    },
    'hide-cursor': {
      type: Boolean,
      default: false
    }
  },
  created () {
    for (let i = 0; i < this.required.length; i++) {
      if (!this[this.required[i]]) {
        console.error(`${this.required[i]} is required for ${this.name}!`)
      }
    }
  },
  data () {
    return {
      name: 'navigation-group',
      required: []
    }
  },
  computed: {
    itemList () {
      return this.list ? this.list : []
    },
    itemSpacing () {
      return {
        [`ml-${this.margin}`]: 'active'
      }
    },
    fontClasses () {
      return {
        [this.fontSize]: 'active',
        [`font-weight-${this.fontWeight}`]: 'active'
      }
    },
    itemStyle () {
      return {
        '--font-color': this.$vuetify.theme.primary
      }
    }
  },
  methods: {
    setActive (index) {
      this.$emit('itemSelect', this.list[index].value)
      for (let i = 0; i < this.list.length; i++) {
        this.list[i].active = index === i
      }
    },
    activeClass (active) {
      return {
        [`item-${active ? 'selected' : 'selection'}`]: 'active'
      }
    }
  }
}
</script>

<style scoped>
.text-position {
  margin: 0;
}
.cursor-link {
  cursor: pointer;
}
.item-selected {
  color: var(--font-color);
}
.item-selection {
  display: inline-block;
}
.item-selection:after {
  content: '';
  width: 0px;
  height: 3px;
  display: block;
  background: var(--font-color);
  transition: 200ms;
}
.item-selection:hover:after {
  width: 100%;
}
</style>
