<template>
  <div id="grid-cards">
    <v-layout>
    <v-flex xs12>
      <v-container grid-list-sm fluid class="container-padding">
        <v-layout row wrap>
          <v-flex
            v-for="(item, index) in list"
            v-bind:key="index"
            class="pa-0"
            xs4
            d-flex
          >
            <v-hover>
              <v-card slot-scope="{hover}"
                tile
                class="d-flex"
                style="cursor: pointer;"
                :style="`border-radius: ${hover ? 10 : 0}px`"
                :class="[cardClass, `elevation-${hover ? 10 : 0}`]"
                @click.native="$emit('itemSelect', item)"
              >
                <v-img
                  v-if="imageKey"
                  :src="item[imageKey]"
                  :lazy-src="lazyload"
                  :aspect-ratio="aspectRatio"
                >
                  <v-expand-transition>
                    <div
                      v-if="hover"
                      style="height: 100%"
                      class="d-flex transition-fast-in-fast-out"
                    >
                      <v-layout
                        v-if="captionKey"
                        fill-height
                        align-center
                        justify-center
                        ma-0
                      >
                        <div class="ma-5 item-title-wrapper" :style="titleStyle"><b>{{ item[captionKey] }}</b></div>
                      </v-layout>
                    </div>
                  </v-expand-transition>
                  <template v-slot:placeholder>
                    <v-layout
                      fill-height
                      align-center
                      justify-center
                      ma-0
                    >
                      <v-progress-circular indeterminate :size="40" :width="3" color="grey lighten-1"></v-progress-circular>
                    </v-layout>
                  </template>
                </v-img>
              </v-card>
            </v-hover>
          </v-flex>
        </v-layout>
      </v-container>
    </v-flex>
    </v-layout>
  </div>
</template>

<script>
export default {
  props: {
    list: Array,
    imageKey: {
      type: String,
      default: 'thumbnail_image'
    },
    captionKey: {
      type: String,
      default: 'name'
    },
    margin: {
      type: Number,
      default: 2
    },
    aspectRatio: {
      type: Number,
      default: 1.5
    }
  },
  created () {
    for (let i = 0; i < this.required.length; i++) {
      if (!this[this.required[i]]) {
        console.error(`${this.required[i]} is required for ${this.name}!`)
      }
    }
  },
  data () {
    return {
      name: 'grid-cards',
      required: ['list']
    }
  },
  computed: {
    cardClass () {
      return {
        [`ma-${this.margin}`]: true
      }
    },
    titleStyle () {
      return {
        color: this.$vuetify.theme.primaryDark,
        backgroundColor: this.$vuetify.theme.primaryLight
      }
    },
    lazyload () {
      let resources = this.$store.getters.resources
      if (resources) {
        return resources.lazyloadImg.grey
      } else {
        return ''
      }
    }
  }
}
</script>

<style scoped>
.container-padding {
  padding: 30px 0;
}
.item-title-wrapper {
  padding: 0 5px;
  text-align: center;
}
</style>
