import Vue from 'vue'
import Router from 'vue-router'
import Projects from '@/views/Projects.vue'
import ProjectView from '@/views/ProjectView.vue'
import About from '@/views/About.vue'
import Connect from '@/views/Connect.vue'
import Snaps from '@/views/Snaps.vue'

Vue.use(Router)

export default new Router({
  // mode: 'history',
  base: process.env.BASE_URL,
  scrollBehavior () {
    return { x: 0, y: 0 }
  },
  routes: [
    {
      // Homepage
      path: '/projects',
      alias: '/',
      name: 'Projects',
      component: Projects
    },
    {
      path: '/projects=:id',
      name: 'ProjectView',
      component: ProjectView,
      props: true
    },
    {
      path: '/about',
      name: 'About',
      component: About
    },
    {
      path: '/connect',
      name: 'Connect',
      component: Connect
    },
    {
      path: '/snaps',
      name: 'Snaps',
      component: Snaps
    }
  ]
})
