<template>
  <div id="home">
    <v-container :class="`${$vuetify.breakpoint.xs ? '' : 'main-container'}`">
      <Navigation-Group
        class="mt-4"
        title="PROJECTS"
        :list="filters"
        @itemSelect="filterList"
      />
      <Grid-Cards
        :list="projects"
        @itemSelect="routeToLink"
      />
      <Footer />
    </v-container>
  </div>
</template>

<script>
import { mapGetters } from 'vuex'
import GridCards from '@/components/GridCards'
import NavigationGroup from '@/components/NavigationGroup'
import Footer from '@/components/Footer'

export default {
  components: {
    GridCards,
    NavigationGroup,
    Footer
  },
  data () {
    return {
      filters: [
        { name: 'Product Development', value: 'pd', active: false },
        { name: 'Interaction Design', value: 'uxui', active: false },
        { name: 'Research', value: 'research', active: false },
        { name: 'All Projects', value: '', active: true }
      ],
      activeFilter: ''
    }
  },
  computed: {
    ...mapGetters('projects', {
      projectsByTag: 'projectsByTag'
    }),
    projects () {
      return this.projectsByTag(this.activeFilter)
    }
  },
  methods: {
    filterList (str) {
      this.activeFilter = str
    },
    routeToLink (obj) {
      this.$router.push({ name: 'ProjectView', params: { id: obj.id } })
    }
  }
}
</script>
