  <div>
    <v-toolbar v-if="!$vuetify.breakpoint.xs"
      flat app scroll-off-screen extended
      extension-height="78"
      :scroll-threshold=50
      color="white"
    >
      <v-layout row>
        <v-spacer></v-spacer>
        <div class="nav-header">
          <p class="nav-signature-gradient" :style="navSignatureStyle">- Vic Ong -</p>
        </div>
        <v-spacer></v-spacer>
      </v-layout>
      <v-layout row wrap slot="extension">
        <v-spacer></v-spacer>
        <router-link
          v-for="(item, index) in menu"
          v-bind:key="index"
          :to="{ name: item.router }"
          @click.native="setActive(index)"
        >
          <div
            class="nav-link-select"
            :style="navLinkStyle"
          >
            {{ item.title }}
          </div>
        </router-link>
        <v-spacer></v-spacer>
        <v-flex xs12 class="seperator primary"></v-flex>
      </v-layout>
    </v-toolbar>

    <!-- Mobile view -->
    <v-toolbar v-else>
      <h1>Current Page</h1>
