<script>
export default {
  data () {
    return {
      menu: [
        { title: 'Projects', router: 'Projects', active: true },
        { title: 'About', router: 'About', active: false },
        { title: 'Connect', router: 'Connect', active: false },
        { title: 'Snaps', router: 'Snaps', active: false }
      ]
    }
  },
  computed: {
    navSignatureStyle () {
      return {
        fontFamily: 'Rock Salt',
        fontSize: '36px',
        paddingTop: '15px'
      }
    },
    navLinkStyle () {
      return {
        padding: '0 50px',
        '--transition-length': '0.3s',
        '--color': this.$vuetify.theme.primaryDark,
        '--color-after': this.$vuetify.theme.primary,
        '--size': '20px',
        '--size-after': '24px'
      }
    }
  },
  methods: {
    setActive (index) {
      for (let i = 0; i < this.menu.length; i++) {
        this.menu[i].active = i === index
      }
    }
  }
}
</script>

