export const snap = state => {
  return state.snap
}

export const snaps = state => {
  return state.snaps
}

export const snapsByBook = getters => {
  const snapsByBookFunc = filter => {
    return getters.snaps.filter(obj => {
      return !filter || obj.category === filter
    })
      .sort((a, b) => {
        return a.pos - b.pos
      })
  }
  return snapsByBookFunc
}
