export const project = state => {
  return state.project
}

export const projects = state => {
  return state.projects
}

export const projectsByTag = getters => {
  const projectByTagFunc = filter => {
    return getters.projects.filter(obj => {
      return obj.tags.indexOf(filter) !== -1 || !filter
    })
      .sort((a, b) => {
        return a.score - b.score
      })
  }
  return projectByTagFunc
}
